import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import CurrencyManager from "../manager/CurrencyManager";
import LTUI from "../../LTGame/UIExt/LTUI";
import UI_Lose from "../../ui/Main/UI_Lose";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";
import LTSDK from "../../SDK/LTSDK";

export default class UI_LoseMediator extends BaseUIMediator<UI_Lose> {

    private static _instance: UI_LoseMediator;
    public static get instance(): UI_LoseMediator {
        if (this._instance == null) {
            this._instance = new UI_LoseMediator();
            this._instance._classDefine = UI_Lose;
        }
        return this._instance;
    }

    private _gold: number;

    _OnShow() {
        super._OnShow();
        // your code
        this._onClick(this._ui.m_btnAd, this._showAd);
        this._onClick(this._ui.m_btnClose, this._onClose);
        // let diceConfig = DiceConfig.data[this._openParam];
        // this._gold = diceConfig.unlockGold;
        // this._ui.m_count.text = `+${this._gold}`;
        // this._ui.m_imgDice.url = diceConfig.icon;
        // this._ui.m_state.selectedIndex = 0;
    }
    _OnHide() { }

    private async _showAd() {
        LTPlatform.instance.RecordEvent("ad_popup", { id: 3, complete: 0 });
        let rst = await LTPlatform.instance.ShowRewardVideoAdAsync();
        if (rst) {
            LTPlatform.instance.RecordEvent("ad_popup", { id: 3, complete: 1 });
            LTUI.Toast(`复活成功`)
            this.Hide();
            this._hideHandler?.runWith(true);
        }
    }
    private _onClose(): void {
        this.Hide();
        this._hideHandler?.runWith(false);
    }
}