import DefaultPlatform from "../../DefaultPlatform";
import { EPlatformType } from "../../EPlatformType";
import LTPlatformData from "../../Data/LTPlatformData";
import LTPlatform from "../../LTPlatform";
import LTUI from "../../../UIExt/LTUI";
import { NativeAndroidDevice } from "./NativeAndroidDevice";

export class NativeAndroidPlatform extends DefaultPlatform {
    useWAV = true;

    platform: EPlatformType = EPlatformType.Native_Android;
    safeArea: LTGame.SafeArea = null;
    bridge: any;
    private _isDebug: boolean = true;;
    protected _cacheScreenScale: number;

    private _cacheRewardSuccess: Laya.Handler;
    private _cacheRewardSkipped: Laya.Handler;
    private _cacheRewardFailed: Laya.Handler;

    Init(platformData: LTPlatformData) {
        this.platformData = platformData;
        let conchConfig = window['conchConfig'];
        let PlatformClass = window['PlatformClass'];
        let os = conchConfig.getOS();
        if (os == "Conch-android") {
            this.bridge = PlatformClass.createClass("demo.JSBridge");//创建脚本代理
            this._InitBridge();
        }
        console.log("平台初始化完成", LTPlatform.platformStr, this);

        this._InitSystemInfo();
        this.loginState = {
            isLogin: true,
            code: "111111111111"
        };
        this.userInfo = { nickName: '未登录用户666', avatarUrl: '66.png' }
        Laya.timer.once(500, this, this.FakeLoginEnd);

        this.device = new NativeAndroidDevice(this);

    }
    protected _InitSystemInfo() {
        try {
            this.safeArea = {} as LTGame.SafeArea;
            this.safeArea.left = 0;
            this.safeArea.right = 0;
            this.safeArea.bottom = 0;
            this.safeArea.top = 0;
            this.safeArea.width = 750;
            this.safeArea.height = 1334;
            this._cacheScreenScale = 750 / Laya.stage.width;
            console.log("初始化安全区域完成");
        } catch (e) {
            console.error(e);
            console.error("获取设备信息失败,执行默认初始化");
            this.safeArea = null;
        }
    }
    private FakeLoginEnd() {
        if (this.onLoginEnd)
            this.onLoginEnd.run();
    }
    ShowRewardVideoAd(onSuccess: Laya.Handler, onSkipped: Laya.Handler, onFailed: Laya.Handler) {
        this._cacheRewardSuccess = onSuccess;
        this._cacheRewardSkipped = onSkipped;
        this._cacheRewardFailed = onFailed;
        LTUI.ShowLoading("Loading...");
        this._CallJavaFunc("ShowRewardVideoAd", null);
    }

    ShowInterstitalAd() {
        this._CallJavaFunc("ShowInterstitalAd", null);
    }

    ShowBannerAd() {
        this._CallJavaFunc("ShowBannerAd", null);
    }

    HideBannerAd() {
        this._CallJavaFunc("HideBannerAd", null);
    }

    private _CallJavaFunc(funcName: string, param: any) {
        let callData = {
            funcName: funcName,
            param: param
        };
        let json = JSON.stringify(callData);
        this.bridge.call("CallFromJS", json);
    }

    private _InitBridge() {
        this.bridge.callWithBack((jsonStr: string) => {
            console.log("-----recv from java:" + jsonStr);
            let backData = JSON.parse(jsonStr) as {
                adtype: EADType,
                result: EBackResult,
            };
            switch (backData.adtype) {
                case EADType.RewardVideo:
                    // 关闭加载界面
                    LTUI.HideLoading();
                    if (backData.result == EBackResult.CloseWithReward) {
                        this._cacheRewardSuccess?.run();
                    } else if (backData.result == EBackResult.LoadFailed) {
                        this._cacheRewardFailed?.run();
                    } else {
                        this._cacheRewardSkipped?.run();
                    }
                    this._cacheRewardSkipped = null;
                    this._cacheRewardSuccess = null;
                    this._cacheRewardFailed = null;
                    break;
            }
        }, "_CallBridge_");
    }

    public CallJavaFunc(funcName: string, param: any) {
        this._CallJavaFunc(funcName, param);
    }

}

enum EADType {

    RewardVideo = 1,

}

enum EBackResult {

    LoadFailed = 1,
    CloseWithReward = 2,
    CloseWithNoReward = 3,

}