import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import UI_Fight from "../../ui/Main/UI_Fight";
import GlobalUnit from "../common/GlobalUnit";
import CurrencyManager from "../manager/CurrencyManager";
import { TaskConfig } from "../config/TaskConfig";
import UI_DiceItem from "../../ui/Main/UI_DiceItem";
import { ITask } from "../common/Interfaces";
import { DiceConfig } from "../config/DiceConfig";
import GameData from "../common/GameData";
import UI_UnlockItem from "../../ui/Main/UI_UnlockItem";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import { Const } from "../common/Const";
import LTSDK from "../../SDK/LTSDK";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";

export default class UI_UnlockItemMediator extends BaseUIMediator<UI_UnlockItem> {

    private static _instance: UI_UnlockItemMediator;
    public static get instance(): UI_UnlockItemMediator {
        if (this._instance == null) {
            this._instance = new UI_UnlockItemMediator();
            this._instance._classDefine = UI_UnlockItem;
        }
        return this._instance;
    }

    private _itemType: Const.ITEM_TYPE;

    _OnShow() {
        super._OnShow();
        // your code
        this._itemType = this._openParam;

        this._onClick(this._ui.m_btnAd, this._showAd);
        this._onClick(this._ui.m_btnClose, this._onClose);
        this._ui.m_type.selectedIndex = this._itemType == Const.ITEM_TYPE.BOMB ? 0 : 1;
    }
    _OnHide() {
        if (LTPlatform.instance.platform != EPlatformType.TT) {
            LTPlatform.instance.ShowBannerAd();
        }
    }

    private async _showAd() {
        let rst = await LTPlatform.instance.ShowRewardVideoAdAsync();
        if (rst) {
            if (this._itemType == Const.ITEM_TYPE.BOMB) {
                GameData.instance.bombCount = GameData.instance.bombCount + 1 > 5 ? 5 : GameData.instance.bombCount + 1;
            } else if (this._itemType == Const.ITEM_TYPE.MAGNET) {
                GameData.instance.magnetCount = GameData.instance.magnetCount + 1 > 5 ? 5 : GameData.instance.magnetCount + 1;
            }
            GameData.SaveToDisk();
            this._hideHandler?.runWith(true);
            this.Hide();
        }
    }
    private _onClose(): void {
        this._hideHandler?.runWith(false);
        this.Hide();
    }
}