
import LTRes from "../../../../LTGame/Res/LTRes";
import ResDefine from "../../../common/ResDefine";
import { Const } from "../../../common/Const";
import { ComTools } from "../../../common/ComTools";
import Vector3Ex from "../../../../LTGame/LTUtils/Vector3Ex";
import { IItem } from "../../../common/Interfaces";
import MathEx from "../../../../LTGame/LTUtils/MathEx";
/**
 * 磁铁
 */
export default class Magnet implements IItem {

    /*********************************************接口 */
    type: Const.ITEM_TYPE = Const.ITEM_TYPE.MAGNET;
    rootObj: Laya.Sprite3D;
    async shoot(tPos: Laya.Vector3) {
        return new Promise((resolve, reject) => {
            let transform = this.rootObj.transform;
            let cPos = transform.position.clone();
            let cRotation = transform.localRotationEuler;
            let tRotation = new Laya.Vector3(90, 0, 0);
            let updataHandler = new Laya.Handler(this, (pro: number) => {
                let np = Vector3Ex.temp;
                Laya.Vector3.lerp(cPos, tPos, MathEx.Clamp01(pro), np);
                transform.position = np;
                Laya.Vector3.lerp(cRotation, tRotation, MathEx.Clamp01(pro), np);
                transform.localRotationEuler = np;
            })
            let endHandler = new Laya.Handler(this, () => {
                resolve()
            });
            ComTools.runTween(updataHandler, 1000, Laya.Ease.quadOut, endHandler);
        });
    }
    /*********************************************end */
    constructor(pos: Laya.Vector3) {
        this.rootObj = LTRes.Get(ResDefine.FixPath(ResDefine.magnet)) as Laya.Sprite3D;
        this.rootObj.transform.position = pos;
    }

    /**
     * 每帧更新时执行
     */
    protected _onLogicUpdate(): void {

    }
    clearItem(): void {

        this.rootObj.removeSelf();
        this.rootObj = null;
    }
}