import DefaultDevice from "../../DefaultDevice";
import { NativeAndroidPlatform } from "./NativeAndroidPlatform";

export class NativeAndroidDevice extends DefaultDevice {

    protected _base: NativeAndroidPlatform;

    constructor(base: any) {
        super();
        this._base = base;
    }

    Vibrate(isLong: boolean) {
        if (isLong) {
            this._base.CallJavaFunc("Vibrator_Long", null);
        } else {
            this._base.CallJavaFunc("Vibrator_Short", null);
        }
    }

}