import { DataManager } from "../manager/DataManager";
import { EffectManager } from "../manager/EffectManager";
import { EventManager } from "../manager/EventManager";
import ViewGame from "../logic/game/ViewGame";
import { Router } from "./Router";
import { EGameState } from "../logic/game/EGameState";
import { OIMOPhysicManager } from "../../LTGame/Physics/OIMOPhysicManager";
import LevelSaveData from "../logic/LevelSaveData";

export default class GlobalUnit {

    static s3d: Laya.Scene3D;

    static dataManager: DataManager;
    static eventMgr: EventManager;
    static game: ViewGame;

    static iconUrl: string = "";
    static InitAll(urls: string[]) {
        this.s3d = Laya.stage.addChildAt(new Laya.Scene3D(), 0) as Laya.Scene3D;
        window['s3d'] = this.s3d;

        OIMOPhysicManager.CreateInstance();
        new LevelSaveData();
        
        this.eventMgr = new EventManager(new Router());
        this.dataManager = new DataManager();
        this.dataManager.InitData();

        this.game = new ViewGame();
        this.game.gamePreload(urls);
        
        window['s3d'] = this.s3d;
        window['GlobalUnit'] = this;
    }

    static ResetScene(scene: Laya.Scene3D) {
        if (this.s3d != null && !this.s3d.destroyed) {
            EffectManager.instance.UnloadScene();
            this.s3d.destroy();
        }
        this.s3d = scene;
        Laya.stage.addChildAt(this.s3d, 0);
        EffectManager.instance.ReAddScene();
        window['s3d'] = this.s3d;
    }

    static async FirstCreate() {

        OIMOPhysicManager.instance.InitWorld();
        this.game.fsm.ChangeState(EGameState.Main);
    }

}