import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import LTUI from "../../LTGame/UIExt/LTUI";
import UI_ReadSaveData from "../../ui/Main/UI_ReadSaveData";
import { ITask } from "../common/Interfaces";
import GlobalUnit from "../common/GlobalUnit";
import { TaskConfig } from "../config/TaskConfig";
import UI_DiceItem from "../../ui/Main/UI_DiceItem";
import { DiceConfig } from "../config/DiceConfig";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";
import LTSDK from "../../SDK/LTSDK";

export default class UI_ReadSaveDataMediator extends BaseUIMediator<UI_ReadSaveData> {

    private static _instance: UI_ReadSaveDataMediator;
    public static get instance(): UI_ReadSaveDataMediator {
        if (this._instance == null) {
            this._instance = new UI_ReadSaveDataMediator();
            this._instance._classDefine = UI_ReadSaveData;
        }
        return this._instance;
    }

    _OnShow() {
        super._OnShow();
        // your code
        this._onClick(this._ui.m_btnAd, this._showAd);
        this._onClick(this._ui.m_btnClose, this._onClose);
        this._initTaskPro();
    }
    _OnHide() { }

    private async _showAd() {
        LTPlatform.instance.RecordEvent("ad_popup", { id: 1, complete: 0 });
        let rst = await LTPlatform.instance.ShowRewardVideoAdAsync();
        if (rst) {
            LTPlatform.instance.RecordEvent("ad_popup", { id: 1, complete: 1 });
            this.Hide();
            this._hideHandler?.runWith(true);
        }
    }
    private _onClose(): void {
        this.Hide();
        this._hideHandler?.runWith(false);
    }


    private _taskData: ITask[];
    private _endTaskId: number;
    private _initTaskPro(): void {

        let curTaskId = GlobalUnit.game.level.curTaskId;
        this._taskData = [];
        let taskLen = TaskConfig.dataList.length;
        this._endTaskId = TaskConfig.dataList[taskLen - 1].id;
        let taskData: ITask[] = [];
        for (let i = 0; i < taskLen; i++) {
            let taskConfig = TaskConfig.dataList[i];
            let id = taskConfig.id;
            let diceId = taskConfig.targetId;
            taskData[taskData.length] = {
                /**id */
                taskId: id,
                /**骰子id */
                diceId: diceId,
                /**完成状态 0 已达成；1 目标达成；2 未达成 */
                state: curTaskId > id ? 0 : id == curTaskId ? 1 : 2
            }
        }
        this._taskData = taskData;

        this._ui.m_taskItems.m_taskList.setVirtual();
        this._ui.m_taskItems.m_taskList.itemRenderer = new Laya.Handler(this, this._onRenderItem);
        this._ui.m_taskItems.m_taskList.numItems = taskLen;

        let pro = 0;
        if (this._endTaskId == curTaskId) {
            pro = 100;
        } else if (this._endTaskId == curTaskId + 1) {
            pro = 66;
        } else if (curTaskId > 1) {
            pro = 33;
        }
        this._ui.m_taskItems.m_taskPro.tweenValue(pro, 0.3);
        if (curTaskId > 2) {
            this._ui.m_taskItems.m_taskList.scrollToView(curTaskId - 2, false, true);
        }
    }

    private _onRenderItem(idx: number, item: UI_DiceItem): void {
        let data = this._taskData[idx];
        let id = data.diceId;
        let icon = DiceConfig.data[id].icon;
        item.m_dice.url = icon;
        item.m_state.selectedIndex = data.state;
    }
}