export namespace DiceConfig {
    export class config {
        /** 骰子id */ readonly id: number;
        /** 路径 */ readonly path: string;
        /** 名字 */ readonly name: string;
        /** 合成骰子id */ readonly compositeId: number;
        /** 合成当前骰子奖励的金币 */ readonly awardGold: number;
        /** 解锁奖励 */ readonly unlockGold: number;
        /** 图标 */ readonly icon: string;
    }
    export var isConst: boolean = false;
    export var data: {[key: number]: DiceConfig.config};
    export var dataList: DiceConfig.config[];
    export var lastData: DiceConfig.config;
    export const path = "res/config/DiceConfig.json";
}


