import { LayerDefine } from "../../../common/LayerDefine";
import { OimoHelper } from "../../../../LTGame/Physics/OimoHelper";
import { OIMOPhysicManager } from "../../../../LTGame/Physics/OIMOPhysicManager";
import { IWall } from "../../../common/Interfaces";
/**
 * 游戏失败碰撞体
 */
export default class End  implements IWall{

    public rootObj: Laya.Sprite3D;

    /**刚体 碰撞组 碰撞类型*/
    protected _rig: OIMO.RigidBody;
    collisionGroup: number = LayerDefine.End;
    collisionMask: number = LayerDefine.Dice;
    protected _rigType: OIMO.RigidBodyType = OIMO.RigidBodyType.KINEMATIC;

    constructor(spr: Laya.Sprite3D) {
        this.rootObj = spr;
        //创建刚体
        let transform = this.rootObj.transform;
        this._rig = OimoHelper.CreateRigbody(transform.position, transform.rotation, this._rigType);
        let shape = OimoHelper.CreateShapeByObj(this.rootObj, this.collisionGroup, this.collisionMask);
        this._rig.addShape(shape);
        this._rig.sleep();
        this._rig.userData = this;
        shape.setFriction(0);

        OIMOPhysicManager.instance.world.addRigidBody(this._rig);
        this.rootObj["__owner__"] = this;
    }

    /**
     * 每帧更新时执行
     */
    protected _onLogicUpdate(): void {

    }
    clear(): void {
        OIMOPhysicManager.instance.world.removeRigidBody(this._rig);
        this.rootObj = null;
    }
}