import { IRouter } from "../common/Router";

export class EventManager {

    private _router: IRouter;
    static instance: EventManager;

    constructor(router: IRouter) {
        EventManager.instance = this;
        EventManager.instance._router = router;
    }
    /**
     * 派发事件。
     * @param type	事件类型。
     * @param data	（可选）回调数据。<b>注意：</b>如果是需要传递多个参数 p1,p2,p3,...可以使用数组结构如：[p1,p2,p3,...] ；如果需要回调单个参数 p ，且 p 是一个数组，则需要使用结构如：[p]，其他的单个参数 p ，可以直接传入参数 p。
     * @return 此事件类型是否有侦听者，如果有侦听者则值为 true，否则值为 false。
     */
    static send(type: string, data?: any): void {
        EventManager.instance._router.event(type, data);
    }
    /**
     * 使用 EventDispatcher 对象注册指定类型的事件侦听器对象，以使侦听器能够接收事件通知。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @param args		（可选）事件侦听函数的回调参数。
     * @return 此 EventDispatcher 对象。
     */
    static on(type: string, caller: any, listener: Function, args?: Array<any>): void {
        EventManager.instance._router.on(type, caller, listener, args);
    }
    /**
     * 使用 EventDispatcher 对象注册指定类型的事件侦听器对象，以使侦听器能够接收事件通知，此侦听事件响应一次后自动移除。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @param args		（可选）事件侦听函数的回调参数。
     * @return 此 EventDispatcher 对象。
     */
    static once(type: string, caller: any, listener: Function, args?: Array<any>): void {
        EventManager.instance._router.once(type, caller, listener, args);
    }
    /**
     * 从 EventDispatcher 对象中删除侦听器。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @param onceOnly	（可选）如果值为 true ,则只移除通过 once 方法添加的侦听器。
     * @return 此 EventDispatcher 对象。
     */
    static off(type: string, caller: any, listener: Function, onceOnly?: boolean): void {
        EventManager.instance._router.off(type, caller, listener, onceOnly);
    }
    /**
     * 从 EventDispatcher 对象中删除指定事件类型的所有侦听器。
     * @param type	（可选）事件类型，如果值为 null，则移除本对象所有类型的侦听器。
     * @return 此 EventDispatcher 对象。
     */
    static offAll(type?: string): void {
        EventManager.instance._router.offAll(type);
    }
    /**
     * 移除caller为target的所有事件监听
     * @param	caller caller对象
     */
    static offAllCaller(caller: any): void {
        EventManager.instance._router.offAllCaller(caller);
    }

    /**
     * 路由事件。
     * @param type	事件类型。
     * @param data	（可选）回调数据。<b>注意：</b>如果是需要传递多个参数 p1,p2,p3,...可以使用数组结构如：[p1,p2,p3,...] ；如果需要回调单个参数 p ，且 p 是一个数组，则需要使用结构如：[p]，其他的单个参数 p ，可以直接传入参数 p。
     * @return 此事件类型是否有侦听者，如果有侦听者则值为 true，否则值为 false。
     */
    static route(type: string, data?: any): boolean {
        if (data === void 0) return EventManager.instance._router.event(type);
        return EventManager.instance._router.event(type, data);
    }

    /**
     * 使用对象注册指定类型的消息侦听器对象，以使侦听器能够接收消息通知。
     * @param type		消息的类型。
     * @param caller	消息侦听函数的执行域。
     * @param listener	消息侦听函数。
     * @param args		（可选）消息侦听函数的回调参数。
     * @return 此 EventDispatcher 对象。
     */
    static onMsg(msgtype: number, caller: any, listener: Function, args?: Array<any>): void {
        EventManager.instance._router.on(msgtype.toString(), caller, listener, args);
    }

    /**
     * 从 EventDispatcher 对象中删除侦听器。
     * @param type		消息的类型。
     * @param caller	消息侦听函数的执行域。
     * @param listener	消息侦听函数。
     * @param onceOnly	（可选）如果值为 true ,则只移除通过 once 方法添加的侦听器。
     * @return 此 EventDispatcher 对象。
     */
    static offMsg(msgtype: number, caller: any, listener: Function): void {
        EventManager.instance._router.off(msgtype.toString(), caller, listener)
    }

    /**
     * 移除caller为target的所有消息监听
     * @param	caller caller对象
     */
    static offAllCallerMsg(caller: any): void {
        EventManager.instance._router.offAllCaller(caller);
    }

    /**
     * 路由消息
     * @param type 消息的类型
     * @param data 消息对象
     */
    static routeMsg(msgtype: number, msg: any): void {
        EventManager.instance._router.event(msgtype.toString(), msg)
    }
}