import Awaiters from "../../LTGame/Async/Awaiters";
import CommonSaveData from "../../LTGame/Commom/CommonSaveData";
import { LTUtils } from "../../LTGame/LTUtils/LTUtils";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import { CmpSceneDisplay } from "../../LTGame/UIExt/Cmp/CmpSceneDisplay";
import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import LTUI from "../../LTGame/UIExt/LTUI";
import LTSDK from "../../SDK/LTSDK";
import UI_DiceItem from "../../ui/Main/UI_DiceItem";
import UI_Fight from "../../ui/Main/UI_Fight";
import { ComTools } from "../common/ComTools";
import { Const } from "../common/Const";
import GameData from "../common/GameData";
import { GameEvent } from "../common/GameEvent";
import GlobalUnit from "../common/GlobalUnit";
import { ITask } from "../common/Interfaces";
import { DiceConfig } from "../config/DiceConfig";
import { TaskConfig } from "../config/TaskConfig";
import AudioManager from "../manager/AudioManager";
import CurrencyManager from "../manager/CurrencyManager";
import { EventManager } from "../manager/EventManager";
import UI_UnlockItemMediator from "./UI_UnlockItemMediator";

export default class UI_FightMediator extends BaseUIMediator<UI_Fight> {

    private _downHandler: Laya.Handler;
    private _upHandler: Laya.Handler;
    private _moveHandler: Laya.Handler;

    private _endTaskId: number;

    private _displayScene: CmpSceneDisplay;
    private _flyGoldRoot: Laya.Sprite3D;

    private static _instance: UI_FightMediator;
    public static get instance(): UI_FightMediator {
        if (this._instance == null) {
            this._instance = new UI_FightMediator();
            this._instance._classDefine = UI_Fight;
        }
        return this._instance;
    }

    private _isPressed: boolean;
    private _touchId: number;

    _OnShow() {
        super._OnShow();
        // your code

        this._onClick(this._ui.m_btnBomb, this._changeBomb);
        this._onClick(this._ui.m_btnMagnet, this._changeMangnet);

        this._onClick(this._ui.m_btnAddBomb, this._addBomb);
        this._onClick(this._ui.m_btnAddMagnet, this._addMagnet);
        this._onClick(this._ui.m_btnSound, this._onClickSoundGrp);
        this._onClick(this._ui.m_btnShock, this._onClickShakeGrp);

        this.ui.m_btnBomb.visible = !LTSDK.instance.isInCheck;
        this.ui.m_btnMagnet.visible = !LTSDK.instance.isInCheck;

        /**鼠标事件 */
        this._ui.m_touch.on(Laya.Event.MOUSE_DOWN, this, this._onMouseDown);
        Laya.stage.on(Laya.Event.MOUSE_MOVE, this, this._onMouseMove);
        Laya.stage.on(Laya.Event.MOUSE_UP, this, this._onMouseUp);
        Laya.stage.on(Laya.Event.MOUSE_OUT, this, this._onMouseUp);
        this._initTaskPro();
        this.refreshCount();
        if (LTPlatform.instance.platform != EPlatformType.TT) {
            LTPlatform.instance.ShowBannerAd();
        }
        this._updateToggleGrp();

        this._displayScene = new CmpSceneDisplay(this.ui.m_flyGold);
        this._CreateScene();
    }

    _OnHide() {
        this._displayScene.Dispose();
        this._ui.m_touch.off(Laya.Event.MOUSE_DOWN, this, this._onMouseDown);
        Laya.stage.off(Laya.Event.MOUSE_UP, this, this._onMouseUp);
        Laya.stage.off(Laya.Event.MOUSE_OUT, this, this._onMouseUp);
        Laya.stage.off(Laya.Event.MOUSE_MOVE, this, this._onMouseMove);
        LTPlatform.instance.HideBannerAd();
    }

    public get flyCamera(): Laya.Camera {
        return this._displayScene.camera;
    }

    private async _CreateScene() {
        await this._displayScene.LoadScene('FlyGold');
        this._displayScene.camera.clearColor = new Laya.Vector4(0, 0, 0, 0);
        this._flyGoldRoot = LTUtils.FindChild(this._displayScene.s3d, 'root') as Laya.Sprite3D;
    }

    public PassEvent(event: Laya.Event) {
        this._isPressed = true;
        this._touchId = event.touchId;
        this._downHandler?.runWith(event);
    }

    public addGold(gold: Laya.Sprite3D): void {
        this._flyGoldRoot.addChild(gold);
    }

    protected _updateToggleGrp() {
        let ui = this._ui;
        let data = CommonSaveData.instance;
        ui.m_btnSound.m_switch.selectedIndex = data.isMusicOn ? 1 : 0;
        ui.m_btnShock.m_switch.selectedIndex = data.isShakeOn ? 1 : 0;
    }
    protected _onClickSoundGrp(evt: Laya.Event) {
        AudioManager.instance.PlayById(Const.SOUND.SOUND_1);
        LTUtils.Vibrate(false);
        let data = CommonSaveData.instance;
        data.isMusicOn = !data.isMusicOn;
        CommonSaveData.SaveToDisk();
        this._updateToggleGrp();
        if (data.isMusicOn) {
            AudioManager.instance.CancelMute();
        } else {
            AudioManager.instance.MuteAll();
        }
        AudioManager.instance.PlayById(8);
    }

    protected _onClickShakeGrp(evt: Laya.Event) {
        AudioManager.instance.PlayById(Const.SOUND.SOUND_1);
        let data = CommonSaveData.instance;
        data.isShakeOn = !data.isShakeOn;
        CommonSaveData.SaveToDisk();

        LTUtils.Vibrate(false);
        this._updateToggleGrp();
        AudioManager.instance.PlayById(8);
    }

    private async _changeBomb() {
        LTPlatform.instance.RecordEvent("ad_main", { id: 1, complete: 0 });
        let res = await LTPlatform.instance.ShowRewardVideoAdAsync();
        if (res) {
            LTPlatform.instance.RecordEvent("ad_main", { id: 1, complete: 1 });
            GlobalUnit.game.level.createBomb();
        }
        // if (GameData.instance.bombCount > 0) {
        //     GlobalUnit.game.level.createBomb();
        // } else {
        //     LTUI.Toast("道具不足。");
        //     this._addBomb();
        // }
    }
    private async _changeMangnet() {
        LTPlatform.instance.RecordEvent("ad_main", { id: 2, complete: 0 });
        let res = await LTPlatform.instance.ShowRewardVideoAdAsync();
        if (res) {
            LTPlatform.instance.RecordEvent("ad_main", { id: 2, complete: 1 });
            GlobalUnit.game.level.createMagnet();
        }
        // if (GameData.instance.magnetCount > 0) {
        //     GlobalUnit.game.level.createMagnet();
        // } else {
        //     LTUI.Toast("道具不足。");
        //     this._addMagnet();
        // }
    }
    private async _addBomb() {
        if (GameData.instance.bombCount >= 5) {
            LTUI.Toast("道具上限。");
            return;
        }
        UI_UnlockItemMediator.instance.Show(Const.ITEM_TYPE.BOMB);
        let isRefresh = await UI_UnlockItemMediator.instance.waitPromise();
        if (isRefresh) {
            EventManager.send(GameEvent.NEW_PROP, Const.ITEM_TYPE.BOMB);
            await Awaiters.Seconds(1);
        }
        this.refreshCount();
    }
    private async _addMagnet() {
        if (GameData.instance.magnetCount >= 5) {
            LTUI.Toast("道具上限。");
            return;
        }
        UI_UnlockItemMediator.instance.Show(Const.ITEM_TYPE.MAGNET);
        let isRefresh = await UI_UnlockItemMediator.instance.waitPromise();
        if (isRefresh) {
            EventManager.send(GameEvent.NEW_PROP, Const.ITEM_TYPE.MAGNET);
            await Awaiters.Seconds(1);
        }
        this.refreshCount();
    }

    public async showProp() {
        let num = ComTools.getRandom(0, 1);
        if (num == 0) {
            await this._addBomb();
        } else {
            await this._addMagnet();
        }
    }

    public addHandlerEvent(downHandler: Laya.Handler, upHandler: Laya.Handler, moveHandler: Laya.Handler, ): void {
        this._downHandler = downHandler;
        this._upHandler = upHandler;
        this._moveHandler = moveHandler;
    }

    private _onMouseDown(e: Laya.Event) {
        if (this._isPressed) return;
        this._touchId = e.touchId;
        this._isPressed = true;
        this._downHandler?.runWith(e);
    }

    private _onMouseUp(e: Laya.Event) {
        if (!this._isPressed) return;
        if (e.touchId != this._touchId) return;
        this._isPressed = false;
        this._upHandler?.runWith(e);
    }

    private _onMouseMove(e: Laya.Event) {
        if (!this._isPressed) return;
        if (e.touchId != this._touchId) return;

        this._moveHandler?.runWith(e);
    }

    public refreshCount(): void {
        this._ui.m_btnBomb.m_count.text = `${GameData.instance.bombCount}`;
        this._ui.m_btnBomb.m_count.visible = GameData.instance.bombCount > 0;

        this._ui.m_btnMagnet.m_count.text = `${GameData.instance.magnetCount}`;
        this._ui.m_btnMagnet.m_count.visible = GameData.instance.magnetCount > 0;
    }

    private _taskData: ITask[];
    private _initTaskPro(): void {

        let curTaskId = GlobalUnit.game.level.curTaskId;
        this._taskData = [];
        let taskLen = TaskConfig.dataList.length;
        this._endTaskId = TaskConfig.dataList[taskLen - 1].id;
        let taskData: ITask[] = [];
        for (let i = 0; i < taskLen; i++) {
            let taskConfig = TaskConfig.dataList[i];
            let id = taskConfig.id;
            let diceId = taskConfig.targetId;
            taskData[taskData.length] = {
                /**id */
                taskId: id,
                /**骰子id */
                diceId: diceId,
                /**完成状态 0 已达成；1 目标达成；2 未达成 */
                state: curTaskId > id ? 0 : id == curTaskId ? 1 : 2
            }
        }
        this._taskData = taskData;

        this._ui.m_taskItems.m_taskList.setVirtual();
        this._ui.m_taskItems.m_taskList.itemRenderer = new Laya.Handler(this, this._onRenderItem);
        this._ui.m_taskItems.m_taskList.numItems = taskLen;

        let pro = 0;
        if (this._endTaskId == curTaskId) {
            pro = 100;
        } else if (this._endTaskId == curTaskId + 1) {
            pro = 66;
        } else if (curTaskId > 1) {
            pro = 33;
        }
        this._ui.m_taskItems.m_taskPro.tweenValue(pro, 0.3);
        if (curTaskId > 2) {
            this._ui.m_taskItems.m_taskList.scrollToView(curTaskId - 2, false, true);
        }
    }

    private _onRenderItem(idx: number, item: UI_DiceItem): void {
        let data = this._taskData[idx];
        let id = data.diceId;
        let icon = DiceConfig.data[id].icon;
        item.m_dice.url = icon;
        item.m_state.selectedIndex = data.state;
    }

    public refreshTask(): void {
        let curTaskId = GlobalUnit.game.level.curTaskId;

        for (let i = 0, len = this._taskData.length; i < len; i++) {
            let task = this._taskData[i];
            task.state = curTaskId > task.taskId ? 0 : task.taskId == curTaskId ? 1 : 2
        }
        this._ui.m_taskItems.m_taskList.refreshVirtualList();

        if (curTaskId > 2) {
            this._ui.m_taskItems.m_taskList.scrollToView(curTaskId - 2, true, true);
        }

        let pro = this._ui.m_taskItems.m_taskPro.value;
        let nextValue = 0;
        if (this._endTaskId == curTaskId) {
            nextValue = 100;
        } else if (this._endTaskId == curTaskId + 1) {
            nextValue = 66;
        } else if (curTaskId > 1) {
            nextValue = 33;
        }
        if (pro != nextValue) {
            this._ui.m_taskItems.m_taskPro.tweenValue(nextValue, 0.3);
        }
    }

}