    /**
     * 转盘
     */
    private async _showRollPanel() {
        let openData = new LTG_Com_RollData();
        let handler = new Laya.Handler(this, this._OnRollConfig);
        openData.onRolled = handler;//结算奖励
        openData.onSpecial = handler;//特殊奖励
        openData.Send();
        await openData.WaitClose();
    }
    public async getTrySkinId(isLoad: boolean = true): Promise<number> {

        let handArray = [];
        // TryItemConfig.dataList.filter((config: TryItemConfig.config) => {
        //     return !GlobalUnit.dataManager.IsSkinUnlocked(config.try_id);
        // });
        let len = handArray.length;
        if (len == 0) {
            return null;
        }
        let type = Const.AWARD_TYPE.SKIN;
        let id = 0;
        let path = "";
        let randIndex = MathEx.RandomInt(0, len - 1);
        let ref = handArray[randIndex];
        id = ref.id;
        path = ref.display_model_path;
        if (isLoad) {
            LTUI.ShowLoading('加载中');
            let urls: string[] = [ResDefine.FixPath(path)];
            await LTRes.LoadAsync(urls);
            LTUI.HideLoading();
        }
        return id;
    }
    /**
     * 免费试用
     */
    public async showTrySkinPanel(tryId: number = null, handler: Laya.Handler = null): Promise<boolean> {
        if (!tryId) {
            tryId = await this.getTrySkinId();
        }
        if (null == tryId) {
            return false;
        }
        let ref = TryItemConfig.data[tryId];
        return new Promise((resolve, reject) => {
            let openData = new LTG_Com_TrySkinData();
            openData.modelPath = ref.display_model_path;
            openData.onClose = Laya.Handler.create(this, (isTry: boolean) => {
                if (isTry) {
                    switch (ref.try_type) {
                        case Const.AWARD_TYPE.SKIN:
                            this.level.levelData.trySkinId = ref.id;
                            break;
                        default:
                            break;
                    }
                }
                handler?.runWith(isTry);
                resolve(true)
            });
            openData.Send();
        });
    }
    /**
     * 签到
     */
    public showSignPanel() {
        let openData = new LTG_Com_SignData();
        openData.onSign = Laya.Handler.create(this, (signConfig: SignConfig.config, isWatchAd: boolean) => {
            this.OnGetReward(signConfig.reward_type, signConfig.reward_value);
            if (isWatchAd) {
                this.OnGetReward(signConfig.ad_reward_type, signConfig.ad_reward_value);
            }
        });
        openData.Send();
    }
    /**
     * 彩蛋
     */
    public showEgg(): void {
        let openData = new LTG_Com_EggWallData();
        openData.onUnlocked = Laya.Handler.create(this, this.OnGetReward, null, false);
        // openData.onCloseed = Laya.Handler.create(this, () => {
        //     this.StartGame();
        // }, null, true);
        openData.Send();
    }

