import { BaseGameState } from "./BaseGameState";
import { EGameState } from "./EGameState";
import ViewGame from "./ViewGame";
import UI_FightMediator from "../../ui/UI_FightMediator";
import LTPlatform from "../../../LTGame/Platform/LTPlatform";
import { LTG_Com_ShareVideoData } from "../../../LTG_CommonUI/Data/LTG_Com_ShareVideoData";
import GlobalUnit from "../../common/GlobalUnit";
export class GameStateWin extends BaseGameState {

    constructor(owner: ViewGame) {
        super(owner, EGameState.Win);
    }

    _DoEnter() {
        Laya.timer.once(3000,this,()=>{
            UI_FightMediator.instance.Hide();
            LTPlatform.instance.recordManager.StopRecord(null);
            if (LTPlatform.instance.recordManager.supportRecord) {
                let openData = new LTG_Com_ShareVideoData();
                openData.camera = GlobalUnit.game.camera.rawCamera;
                openData.onClosed = Laya.Handler.create(this, (code: number) => {
                    if (code == 1) {
                        // CurrencyManager.instance.changeCurrency(GameConst.data.share_reward_coin);
                        // LTUI.Toast('获得分享奖励:' + GameConst.data.share_reward_coin + "金币");
                    }
                    this._FinalEnd();
                });
                openData.Send();
            } else {
                this._FinalEnd();
            }
        })
    }

    private _FinalEnd() {
        // UI_WinMediator.instance.Show();
    }

}