import { BaseGameState } from "./BaseGameState";
import ViewGame from "./ViewGame";
import { EGameState } from "./EGameState";
import UI_FightMediator from "../../ui/UI_FightMediator";
import UI_LoseMediator from "../../ui/UI_LoseMediator";
export class GameStateLose extends BaseGameState {

    constructor(owner: ViewGame) {
        super(owner, EGameState.Lose);
    }

    async _DoEnter() {

        UI_FightMediator.instance.Hide();
        UI_LoseMediator.instance.Show();
        let state = await UI_LoseMediator.instance.waitPromise();
        if (state) {
            this.isFinished = true;
            this.nextState = EGameState.Reborn;
        } else {
            this.isFinished = true;
            this.nextState = EGameState.Reset;
        }
    }

}