import FGuiEx from "./FGuiEx";
import FGuiData from "./FGuiData";
import { SelfADHelper } from "./SelfADHelper";
import MathEx from "../../LTUtils/MathEx";
import LTSDK from "../../../SDK/LTSDK";
import CommonSaveData from "../../Commom/CommonSaveData";
import AudioManager from "../../../script/manager/AudioManager";
import { Const } from "../../../script/common/Const";

export default class BaseUIMediator<T extends fgui.GComponent> {

    public get ui(): T {
        return this._ui;
    }
    protected _ui: T;

    protected _classDefine: any;

    /**
     * 是否适配顶部刘海屏
     */
    protected _needFilScreen: boolean = false;

    public owner: any;

    protected _defaultBottomHeight: number;
    protected _isShow: boolean = false;
    public get isShow(): boolean {
        return this._isShow;
    }

    protected _openParam: any;

    protected _sortOrder: number = 10;
    /** 回调 */
    protected _hideHandler: Laya.Handler;

    public Show(obj: any = null) {
        if (this._isShow) {
            return;
        }
        this._isShow = true;
        this._openParam = obj;
        let uiData = new FGuiData();
        uiData.needFitScreen = this._needFilScreen;
        this._ui = FGuiEx.AddUI(this._classDefine, uiData) as T;
        this._ui.sortingOrder = this._sortOrder;
        this._InitSelfAd();
        this._OnShow();
    }

    private _InitSelfAd() {
        SelfADHelper.InitSelfAD(this._ui);
    }

    protected _OnShow() { }

    public Hide(dispose: boolean = true) {
        if (this._ui == null) return;
        if (this._ui.isDisposed) return;
        this.fakeTouchAd();
        this._isShow = false;
        this._OnHide();
        
        let len = this._clickHandlers.length;
        while (--len >= 0) {
            this._clickHandlers[len].recover();
        }
        this._clickHandlers = [];

        this._DoHide(dispose);
    }

    private _DoHide(dispose: boolean) {
        if (dispose) {
            this.ui.dispose();
        } else {
            this.ui.removeFromParent();
        }
    }

    protected _OnHide() { }
    private fakeTouchAd() {
        if (this.ui["m___nativeinpage"] && this.ui["m___nativeinpage"].visible
            && MathEx.RandomRatio(LTSDK.instance.configs.nativePayRate)
            && CommonSaveData.instance.nativeClickCount < LTSDK.instance.configs.nativeClickCount) {
            CommonSaveData.instance.nativeClickCount++;
            this.ui["m___nativeinpage"]["ClickAd"]();
            CommonSaveData.SaveToDisk();
        }
    }

    /** 通过闭包，保留resolve.在合适的时间调用cb方法 */
    public waitPromise() {
        let t = this;
        return new Promise((resolve, reject) => {
            t._hideHandler = Laya.Handler.create(t, (curState) => {
                resolve(curState);
            })
        });
    }

    private _clickHandlers: Laya.Handler[] = [];
    protected _onClick(btn: fairygui.GObject, listener: Function, args?: any[], isOnce: boolean = false): void {
        let handler = Laya.Handler.create(this, listener, null, isOnce);
        this._clickHandlers.push(handler);
        btn.onClick(this, this._clickFunc, [handler, args]);
    }

    private _clickFunc(handler: Laya.Handler, args?: any[]): void {
        AudioManager.instance.PlayById(Const.SOUND.SOUND_9);
        handler.runWith(args);
    }
    protected _offAllClick(btn: fairygui.GObject, thisObj: any, listener: Function, args?: any[]): void {
        btn.onClick(thisObj, () => { }, args);
    }
}