import { BaseGameState } from "./BaseGameState";
import ViewGame from "./ViewGame";
import { EGameState } from "./EGameState";
import UI_FightMediator from "../../ui/UI_FightMediator";
import { Const } from "../../common/Const";

import LEVEL_STATE = Const.LEVEL_STATE;
import LTPlatform from "../../../LTGame/Platform/LTPlatform";
import UI_ReadSaveDataMediator from "../../ui/UI_ReadSaveDataMediator";
import LevelSaveData from "../LevelSaveData";
import { GameConst } from "../../config/GameConst";
export class GameStateFight extends BaseGameState {

    private _curState: LEVEL_STATE;
    
    constructor(owner: ViewGame) {
        super(owner, EGameState.Fight);
    }

    _DoEnter(exitState: BaseGameState, param: any) {
        UI_FightMediator.instance.Show();
        let event = param as Laya.Event;
        switch (exitState.id) {
            case EGameState.Main:
                this._curState = LEVEL_STATE.READY;
                this._FromMain(event);
                break;
            case EGameState.Reborn:
                this.owner.level.state = LEVEL_STATE.RE_GAME;
                break;
        }
    }

    private async _FromMain(event: Laya.Event) {
        //超过2048看广告读取进度
        if(LevelSaveData.instance.levelSaveData.taskId > GameConst.data.taskId){
            UI_ReadSaveDataMediator.instance.Show();
            let isRead = await UI_ReadSaveDataMediator.instance.waitPromise();
            if (isRead) {
                this._StartLevel(event);
            }else{
                UI_FightMediator.instance.Hide();
                this.isFinished = true;
                this.nextState = EGameState.Reset;
            }
        }else{
            this._StartLevel(event);
        }
    }

    private _StartLevel(event: Laya.Event):void{
        LTPlatform.instance.recordManager.StartRecord(null, null);
        this.owner.level.StartLevel(event);
    }

    _DoRunning() {
        this.owner.level.LogicUpdate(this.deltaTime);

        let state = this.owner.level.state;
        this._enterNewState(state);

        // switch (state) {
        //     case LEVEL_STATE.END:
        //         {
        //         }
        //         break;
        //     case LEVEL_STATE.WIN:
        //         {
        //         }
        //         break;
        //     case LEVEL_STATE.LOSE:
        //         {
        //         }
        //         break;
        //     default:
        //         break;
        // }
    }

    private _enterNewState(newState: LEVEL_STATE): void {
        if (this._curState == newState) return;
        this._curState = newState;
        switch (newState) {
            case LEVEL_STATE.WIN:
                {
                    this.isFinished = true;
                    this.nextState = EGameState.Win;
                }
                break;
            case LEVEL_STATE.LOSE:
                {
                    this.isFinished = true;
                    this.nextState = EGameState.Lose;
                }
                break;
            default:
                break;
        }
    }

}