export namespace GameConst {
    export class config {
        /** 游戏版本号 */ readonly game_version: string;
        /** 转多少次获得特殊奖励 */ readonly special_roll_count: number;
        /** 游戏公告 */ readonly notice_str: string;
        /** 抖音号 */ readonly douyin_id: string;
        /** 导量至飞小鸭官方视频，未开通官方账号时使用 */ readonly douyinVideoId: string;
        /** 头条官方视频，未绑定官方账号时使用 */ readonly toutiaoVideoId: string;
        /** 是否已开通官方账号 */ readonly officalAccountEnable: boolean;
        /** 骰子恢复 */ readonly restitution: number;
        /** 骰子摩擦力 */ readonly friction: number;
        /** 骰子发射速度 */ readonly shootVelocity: number[];
        /** 是否开启物理调试 */ readonly isDebugDraw: boolean;
        /** 骰子滑动速度 */ readonly moveSpeed: number;
        /** 合成骰子X速度区间 */ readonly jumpSpeedX: number[];
        /** 合成骰子Y速度区间 */ readonly jumpSpeedY: number[];
        /** 合成骰子Z速度区间 */ readonly jumpSpeedZ: number[];
        /** 重力 */ readonly gravity: number;
        /** 炸弹爆炸范围半径 */ readonly scopeR: number;
        /** 炸弹击飞半径范围 */ readonly knockR: number;
        /** 分享金币 */ readonly share_reward_coin: number;
        /** 完成任务id弹历史记录弹窗 */ readonly taskId: number;
        /** 合成大于骰子id弹解锁新球的窗口 */ readonly popNewDiceId: number;
        /** 合成等于骰子id弹道具与分享窗口 */ readonly popShareAndPropId: number;
        /** 特效不消失的骰子id */ readonly effDiceId: number;
    }
    export var isConst: boolean = true;
    export var data: GameConst.config;
    export const path = "res/config/GameConst.json";
}


