import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import GameData from "../common/GameData";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import { Const } from "../common/Const";
import UI_UnlockNewDice from "../../ui/Main/UI_UnlockNewDice";
import CurrencyManager from "../manager/CurrencyManager";
import { DiceConfig } from "../config/DiceConfig";
import LTUI from "../../LTGame/UIExt/LTUI";
import LTSDK from "../../SDK/LTSDK";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";

export default class UI_UnlockNewDiceMediator extends BaseUIMediator<UI_UnlockNewDice> {

    private static _instance: UI_UnlockNewDiceMediator;
    public static get instance(): UI_UnlockNewDiceMediator {
        if (this._instance == null) {
            this._instance = new UI_UnlockNewDiceMediator();
            this._instance._classDefine = UI_UnlockNewDice;
        }
        return this._instance;
    }

    _OnShow() {
        super._OnShow();
        // your code
        // this._onClick(this._ui.m_btnAd, this._showAd);
        this._onClick(this._ui.m_btnClose, this._onClose);
        let diceConfig = DiceConfig.data[this._openParam];
        if (LTPlatform.instance.platform == EPlatformType.Native_Android) {
            LTPlatform.instance.ShowInterstitalAd();
        }
        if (diceConfig.id % 2 == 1 && !LTSDK.instance.isInCheck) {
            LTPlatform.instance.createShortcut();
        }
    }
    _OnHide() {
        if (!LTSDK.instance.isInCheck && LTPlatform.instance.platform != EPlatformType.Native_Android) {
            LTPlatform.instance.ShowInterstitalAd();
        }
        if (LTPlatform.instance.platform != EPlatformType.TT) {
            LTPlatform.instance.ShowBannerAd();
        }
    }

    private _onClose(): void {
        this.Hide();
        this._hideHandler?.runWith(false);
    }
}