    /**获取随机数 整数*/
    static getRandom(min: number, max: number): number {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    private static _startTime: number = Laya.Browser.now();
    private static _guardTime: number = 0;
    private static _nowTime: number = 0;
    /**
     * 获取浏览器当前时间戳，单位为毫秒。
     */
    static now(): number {
        let layanow = Laya.Browser.now();
        if (this._nowTime < layanow) this._nowTime = layanow;
        return this._nowTime;
    }

    /**
     * 返回当前启动的时间戮，单位为毫秒
     */
    static realtimeSinceStartup(): number {
        // Laya.Brower.now()会出现时间倒退的情况，为了避免时间倒退产生BUG
        let d = this.now() - this._startTime;
        if (d < this._guardTime) { return this._guardTime; }
        this._guardTime = d;
        return d;
    }

    /**
     * 生成8位唯一id
     * @returns {string}
     */
    public static guuid(): string {
        let s4 = () => {
            return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
        }
        return s4() + s4();
    };

    public static levelToString(level: number): string {
        let a = "game/level/Level";
        let b = "";
        if (level < 10) {
            b = `00${level}`
        } else if (level < 100) {
            b = `0${level}`
        } else if (level < 1000) {
            b = `${level}`
        }
        return a + b;
    }

    /**
    * 数字转换成时间
    * @param sumSecond number (单位秒)
    * @param unit 显示单位    
    * @param isShowWord 是否显示汉字
    */
    public static getStringToTimeInfo(sumSecond: number, unit: number = Const.ToUnit.SECOND, isShowWord: boolean = false): string {

        let str = "";
        let dayStr = isShowWord ? "天" : ":";
        let hourStr = isShowWord ? "时" : ":";
        let minuteStr = isShowWord ? "分" : ":";
        let secondStr = isShowWord ? "秒" : "";
        if (sumSecond >= 86400) {
            let st0 = unit > Const.ToUnit.DAY ? '' : Math.floor(sumSecond / 86400) + dayStr;
            let st1 = unit > Const.ToUnit.HOUR ? '' : Math.floor(sumSecond % 86400 / 3600) + hourStr;
            let minute = Math.floor(sumSecond % 3600 / 60);
            let st2 = unit > Const.ToUnit.MINUTE ? '' : (minute < 10 ? "0" + minute : minute) + minuteStr;
            let second = Math.floor(sumSecond % 60);
            let st3 = unit > Const.ToUnit.SECOND ? '' : (second < 10 ? "0" + second : second) + secondStr;
            str = st0 + st1 + st2 + st3;
            return str;
        } else if (sumSecond >= 3600) {
            let st1 = unit > Const.ToUnit.HOUR ? '' : Math.floor(sumSecond / 3600) + hourStr;
            let minute = Math.floor(sumSecond % 3600 / 60);
            let st2 = unit > Const.ToUnit.MINUTE ? '' : (minute < 10 ? "0" + minute : minute) + minuteStr;
            let second = Math.floor(sumSecond % 60);
            let st3 = unit > Const.ToUnit.SECOND ? '' : (second < 10 ? "0" + second : second) + secondStr;
            str = st1 + st2 + st3;
            return str;
        } else if (sumSecond >= 60) {
            let fen = Math.floor(sumSecond / 60);
            let fenstr = fen < 10 ? "0" + fen : fen;
            let miao = Math.floor(sumSecond % 60);
            let str1 = unit > Const.ToUnit.MINUTE ? '' : fenstr + minuteStr;
            let str2 = unit > Const.ToUnit.SECOND ? '' : (miao < 10 ? "0" + miao : miao) + secondStr;
            str = str1 + str2;
            return str;
        } else {
            let temp = sumSecond < 10 ? "0" : "";
            let s = "00:" + temp + sumSecond.toFixed(0) + secondStr;
            return s;
        }
    }
    /**
     * 凌晨时间
     */
    public static getZeroTime(): number {

        let start = new Date()
        start.setHours(0)
        start.setMinutes(0)
        start.setSeconds(0)
        start.setMilliseconds(0)
        return start.getTime() / 1000
    }
    /**
     * 延迟显示按钮
     * @param btn 
     * @param time 
     */
    public static showLater(btn: fairygui.GButton, time: number): void {
        btn.visible = false;
        Laya.timer.once(time, this, () => {
            btn && (btn.visible = true);
        });
    }

    /**
     * 字符串的省略效果
     */
    public static cutStr(str: string, len: number): string {
        let str_length = 0
        let str_len = 0
        let str_cut: string = ""
        str_len = str.length
        for (let i = 0; i < str_len; i++) {
            let a = str.charAt(i)
            str_length++
            if (encodeURI(a).length > 4) {
                //中文字符的长度经编码之后大于4
                str_length++
            }
            str_cut = str_cut.concat(a)
            if (str_length >= len) {
                if (i < str_len - 1) {

                    str_cut = str_cut.concat("...")
                }
                return str_cut
            }
        }
        //如果给定字符串小于指定长度，则返回源字符串；
        if (str_length < len) {
            return str
        }
    }

