/**
 * 回调队列
 */
export default class CallQueue {
    private _quene: Laya.Handler[] = [];

    /**
     * 添加回调
     * @param handler
     */
    public addCall(handler: Laya.Handler) {
        this._quene.push(handler)
    }

    /**
     * 执行下一个回调
     */
    public nextCall() {
        let handler = this._quene.shift()
        if (handler) {
            handler.run();
        }
    }

    /**
     * 回调队列长度
     */
    public getCallLength(): number {

        return this._quene.length
    }
}