import { BaseGameState } from "./BaseGameState";
import ViewGame from "./ViewGame";
import { EGameState } from "./EGameState";
import LTRes from "../../../LTGame/Res/LTRes";
import { UI_MainMediator } from "../../ui/UI_MainMediator";
import LTUI from "../../../LTGame/UIExt/LTUI";
import GameData from "../../common/GameData";

export class GameStateReset extends BaseGameState {

    constructor(owner: ViewGame) {
        super(owner, EGameState.Reset);
    }

    private _handler: Laya.Handler;

    _DoEnter(exitState: BaseGameState, param: any) {
        this._AsyncAction();
    }

    private async _AsyncAction() {
        LTUI.ShowLoading('资源加载中');
        this.owner.ClearGame();
        let urls = [];
        this.owner.lvPreload(urls);
        await LTRes.LoadAsync(urls);

        LTUI.HideLoading();
        this.isFinished = true;
        this.nextState = EGameState.Main;
    }
}