import BaseUIMediator from "../../LTGame/UIExt/FGui/BaseUIMediator";
import UI_Main from "../../ui/Main/UI_Main";
import GlobalUnit from "../common/GlobalUnit";
import CurrencyManager from "../manager/CurrencyManager";
import CommonSaveData from "../../LTGame/Commom/CommonSaveData";
import AudioManager from "../manager/AudioManager";
import { Const } from "../common/Const";
import { LTUtils } from "../../LTGame/LTUtils/LTUtils";
import { LTG_Com_StatementData } from "../../LTG_CommonUI/Data/LTG_Com_StatementData";
import LTPlatform from "../../LTGame/Platform/LTPlatform";
import { EPlatformType } from "../../LTGame/Platform/EPlatformType";

export class UI_MainMediator extends BaseUIMediator<UI_Main> {

    private static _instance;
    public static get instance(): UI_MainMediator {
        if (this._instance == null) {
            this._instance = new UI_MainMediator();
            this._instance._classDefine = UI_Main;
        }
        return this._instance;
    }

    _OnShow() {
        super._OnShow();

        this.ui.m_start.on(Laya.Event.MOUSE_DOWN, this, this._OnMouseDown);
        this._onClick(this._ui.m_btnSound, this._onClickSoundGrp);
        this._onClick(this._ui.m_btnShock, this._onClickShakeGrp);
        this._updateToggleGrp();
    }

    protected _updateToggleGrp() {
        let ui = this._ui;
        let data = CommonSaveData.instance;
        ui.m_btnSound.m_switch.selectedIndex = data.isMusicOn ? 1 : 0;
        ui.m_btnShock.m_switch.selectedIndex = data.isShakeOn ? 1 : 0;
    }

    private _OnMouseDown(event: Laya.Event) {
        this.Hide();
        GlobalUnit.game.StartGame(event);
    }

    protected _onClickSoundGrp(evt: Laya.Event) {
        AudioManager.instance.PlayById(Const.SOUND.SOUND_1);
        LTUtils.Vibrate(false);
        let data = CommonSaveData.instance;
        data.isMusicOn = !data.isMusicOn;
        CommonSaveData.SaveToDisk();
        this._updateToggleGrp();
        if (data.isMusicOn) {
            AudioManager.instance.CancelMute();
        } else {
            AudioManager.instance.MuteAll();
        }
        AudioManager.instance.PlayById(8);

    }

    protected _onClickShakeGrp(evt: Laya.Event) {
        AudioManager.instance.PlayById(Const.SOUND.SOUND_1);
        let data = CommonSaveData.instance;
        data.isShakeOn = !data.isShakeOn;
        CommonSaveData.SaveToDisk();

        LTUtils.Vibrate(false);
        this._updateToggleGrp();
        AudioManager.instance.PlayById(8);
    }

}