import LTRes from "../../../../LTGame/Res/LTRes";
import ResDefine from "../../../common/ResDefine";
import { Const } from "../../../common/Const";
import { DiceConfig } from "../../../config/DiceConfig";
import { LayerDefine } from "../../../common/LayerDefine";
import { ComTools } from "../../../common/ComTools";
import { OimoHelper } from "../../../../LTGame/Physics/OimoHelper";
import { GameConst } from "../../../config/GameConst";
import { OIMOPhysicManager } from "../../../../LTGame/Physics/OIMOPhysicManager";
import Vector3Ex from "../../../../LTGame/LTUtils/Vector3Ex";
import GlobalUnit from "../../../common/GlobalUnit";
import { IItem, IDiceSaveData } from "../../../common/Interfaces";
import MathEx from "../../../../LTGame/LTUtils/MathEx";
import Awaiters from "../../../../LTGame/Async/Awaiters";
import DrawLine from "../../../common/DrawLine";
import { EffectManager } from "../../../manager/EffectManager";
import LTUI from "../../../../LTGame/UIExt/LTUI";
import QuaternionEx from "../../../../LTGame/LTUtils/QuaternionEx";
import LevelSaveData from "../../LevelSaveData";
/**
 * 骰子
 */
export default class Dice implements IItem {

    public ref: DiceConfig.config;
    /**状态 */
    private _state: Const.ITEM_STATE;
    /**刚体 碰撞组 碰撞类型*/
    protected _rig: OIMO.RigidBody;
    collisionGroup: number = LayerDefine.Dice;
    private _collisionMask: number = LayerDefine.Dice | LayerDefine.Plane | LayerDefine.Wall | LayerDefine.Bomb;
    private set collisionMask(collisionMask: number) {
        this._collisionMask = collisionMask;
        if (this._rig) {
            this._rig.getShapeList().setCollisionMask(this._collisionMask);
        }
    }
    /**缓存的刚体速度 */
    private _linearVelocity: OIMO.Vec3;
    private _angularVelocity: OIMO.Vec3;
    // private _cacheRigMat3: OIMO.Mat3;
    /**唯一标识 */
    private _onlyId: number;
    public get onlyId(): number { return this._onlyId; }
    /**拖尾特效id */
    private _tailingId: number;
    private _cacheEffectId: number;
    /*********************************************接口 */
    type: Const.ITEM_TYPE = Const.ITEM_TYPE.DICE;
    rootObj: Laya.Sprite3D;
    shoot(): void {
        this.state = Const.ITEM_STATE.SHOOT;
    }
    /*********************************************end */

    /**save data */
    private _diceSaveData: IDiceSaveData;
    public get diceSaveData(): IDiceSaveData { return this._diceSaveData; }

    public static getDice(id: number, position: Laya.Vector3, rotation: Laya.Quaternion = null): Dice {
        let dice = Laya.Pool.getItemByCreateFun(`dice_${id}`, () => {
            let dice = new Dice(id);
            dice._initDice(position);
            return dice;
        }) as Dice;
        dice._onEnter(position, rotation);
        return dice;
    }
    public static readDiceBySaveData(saveData: IDiceSaveData): Dice {
        let position = saveData.position;
        let rotation = saveData.rotation;
        let dice = this.getDice(saveData.id, new Laya.Vector3(position.x, position.y, position.z), new Laya.Quaternion(rotation.x, rotation.y, rotation.z, rotation.w));
        dice.state = saveData.state;
        return dice;
    }

    constructor(id: number) {
        let ref = DiceConfig.data[id];
        this.ref = ref;
    }
    /**初始化 */
    private _initDice(position: Laya.Vector3): void {
        this.rootObj = LTRes.Get(ResDefine.FixPath(this.ref.path)) as Laya.Sprite3D;
        this.rootObj["__owner__"] = this;
        this._linearVelocity = new OIMO.Vec3();
        this._angularVelocity = new OIMO.Vec3();

        this._onlyId = this.rootObj.id;
        //创建刚体
        let transform = this.rootObj.transform;
        this._rig = OimoHelper.CreateRigbody(position, transform.rotation, OIMO.RigidBodyType.KINEMATIC);
        let shape = OimoHelper.CreateShapeByObj(this.rootObj, this.collisionGroup, this._collisionMask, 0.95);
        shape.setRestitution(GameConst.data.restitution);
        this._rig.addShape(shape);
        this._rig.sleep();
        this._rig.userData = this;
        let contactCallback = shape.getContactCallback();
        contactCallback = new OIMO.ContactCallback();
        contactCallback.beginContact = this._beginContact.bind(this);
        // contactCallback.preSolve = this._preSolve.bind(this);
        // contactCallback.postSolve = this._postSolve.bind(this);
        // contactCallback.endContact = this._endContact.bind(this);
        shape.setContactCallback(contactCallback);

        this._diceSaveData = {
            id: this.ref.id,
            state: 0,
            position: { x: 0, y: 0, z: 0 },
            rotation: { x: 0, y: 0, z: 0, w: 0 }
        }
    }
    /**创建完成 */
    private _onEnter(position: Laya.Vector3, rotation: Laya.Quaternion = null): void {
        this.state = Const.ITEM_STATE.READY;
        this.rootObj.transform.position = position;
        if (rotation) {
            this.rootObj.transform.rotation = rotation;
        } else {
            this.rootObj.transform.rotationEuler = Vector3Ex.zero;
        }
        OIMOPhysicManager.instance.world.addRigidBody(this._rig);
        OimoHelper.SyncTransToRig(this.rootObj.transform, this._rig);
        // console.error("创建保存");
        LevelSaveData.instance.saveLevelData();

        //2048特效不消失
        if (this.ref.id == GameConst.data.effDiceId) {
            this._cacheEffectId = ComTools.playEffect(Const.EFFECT_ID.ID_2, null, null, null, this.rootObj, null, new Laya.Vector3(0.9, 0.9, 0.9));
        }
    }
    /**
     * 起始点创建动画
     */
    public playStartTween(): void {
        let scale = Vector3Ex.s_zero;
        this.rootObj.transform.setWorldLossyScale(scale);
        let updataHandler = new Laya.Handler(this, (pro: number) => {
            scale.setValue(pro, pro, pro);
            this.rootObj.transform.setWorldLossyScale(scale);
        });
        ComTools.runTween(updataHandler, 500, Laya.Ease.backOut);
    }
    /**
     * 飞行动画
     * @param tPos 
     */
    public async playFlyTween(tPos: Laya.Vector3, pix: Laya.PixelLineSprite3D) {
        return new Promise((resolve, reject) => {
            this.state = Const.ITEM_STATE.FLY;
            let transform = this.rootObj.transform;
            let cPos = transform.position.clone();
            // let cRotation = transform.localRotationEuler;
            // let tRotation = new Laya.Vector3(90, 0, 0);
            let updataHandler = new Laya.Handler(this, (pro: number) => {
                let np = Vector3Ex.temp;
                Laya.Vector3.lerp(cPos, tPos, MathEx.Clamp01(pro), np);
                transform.position = np;
                // Laya.Vector3.lerp(cRotation, tRotation, MathEx.Clamp01(pro), np);
                // transform.localRotationEuler = np;
                pix.clear();
                this.drawLighting(pix, np, tPos, 0.8);
            })
            let endHandler = new Laya.Handler(this, async () => {
                pix.clear();
                resolve();
            });
            ComTools.runTween(updataHandler, 1000, Laya.Ease.quadInOut, endHandler);
        });
    }
    // 画一条闪电。闪电由多条线段组成。参数displace影响闪电的剧烈程度，值越大越剧烈。
    public drawLighting(pix: Laya.PixelLineSprite3D, pos1: Laya.Vector3, pos2: Laya.Vector3, displace: number) {
        if (displace < 0.2) {
            pix.addLine(pos1, pos2, Laya.Color.WHITE, Laya.Color.WHITE);
        } else {
            let mid_x = (pos1.x + pos2.x) / 2;
            let mid_y = (pos1.y + pos2.y) / 2;
            let mid_z = (pos1.z + pos2.z) / 2;
            mid_x += (Math.random() - 0.5) * displace;
            mid_y += (Math.random() - 0.5) * displace;
            mid_z += (Math.random() - 0.5) * displace;
            let temp = new Laya.Vector3(mid_x, mid_y, mid_z);
            pix.addLine(pos1, temp, Laya.Color.WHITE, Laya.Color.WHITE);
            pix.addLine(pos2, temp, Laya.Color.WHITE, Laya.Color.WHITE);
            this.drawLighting(pix, pos1, temp, displace / 2);
            this.drawLighting(pix, pos2, temp, displace / 2);
        }
    }

    /************************碰撞事件 */
    protected _beginContact(c: OIMO.Contact) {
        let data1 = c.getShape1().getRigidBody().userData;
        let data2 = c.getShape2().getRigidBody().userData;
        let other = data1 == this ? data2 : data1;
        this._onTrigger(other);
    }
    protected _preSolve(c: OIMO.Contact) { }
    protected _postSolve(c: OIMO.Contact) { }
    protected _endContact(c: OIMO.Contact) { }

    protected async _onTrigger(other: any) {
        let collisionGroup = other.collisionGroup;
        switch (collisionGroup) {
            case LayerDefine.Dice:
                if ((Const.ITEM_STATE.PLAYING == this.state || Const.ITEM_STATE.SHOOT == this.state)
                    && (Const.ITEM_STATE.PLAYING == other.state || Const.ITEM_STATE.SHOOT == other.state)) {
                    if (this.ref.id == other.ref.id) {
                        this.state = Const.ITEM_STATE.RECOVER;
                        other.state = Const.ITEM_STATE.RECOVER;

                        let selfPos = this.rootObj.transform.position;
                        let otherPos = other.rootObj.transform.position;
                        let pos = Vector3Ex.Lerp(selfPos, otherPos, 0.5);
                        GlobalUnit.game.level.createNextDice(this.ref.compositeId, pos);
                    }
                }
                break;
            case LayerDefine.Plane:

                break;
            case LayerDefine.Wall:

                break;
            case LayerDefine.End:
                GlobalUnit.game.level.state = Const.LEVEL_STATE.LOSE;
                break;
            default:
                break;
        }
    }

    /**停止动画 */
    stopTween(): void {

    }
    /**
     * 每帧更新时执行
     */
    public onLogicUpdate(): void {

        if (!this._rig) return;
        //刷新状态
        switch (this._state) {
            case Const.ITEM_STATE.READY:
                {
                    OimoHelper.SyncTransToRig(this.rootObj.transform, this._rig);
                }
                break;
            case Const.ITEM_STATE.SHOOT:
                {
                    OimoHelper.SyncTransFromRig(this.rootObj.transform, this._rig);

                    let zv = this._linearVelocity.z;
                    this._rig.getLinearVelocityTo(this._linearVelocity);

                    if (zv != this._linearVelocity.z) {
                        this.state = Const.ITEM_STATE.PLAYING;
                        if(this.rootObj.transform.localPositionZ <= -2.7){
                            GlobalUnit.game.level.state = Const.LEVEL_STATE.LOSE;
                        }
                    }
                }
                break;
            case Const.ITEM_STATE.JUMP:
                {
                    OimoHelper.SyncTransFromRig(this.rootObj.transform, this._rig);
                    this._rig.getLinearVelocityTo(this._linearVelocity);
                    if (this._linearVelocity.y <= 0) {
                        this.state = Const.ITEM_STATE.PLAYING;
                    }
                }
                break;
            case Const.ITEM_STATE.FLY:
                {
                    OimoHelper.SyncTransToRig(this.rootObj.transform, this._rig);
                }
                break;
            case Const.ITEM_STATE.PLAYING:
                {
                    OimoHelper.SyncTransFromRig(this.rootObj.transform, this._rig);
                }
                break;
            case Const.ITEM_STATE.PAUSE:
                {
                    OimoHelper.SyncTransToRig(this.rootObj.transform, this._rig);
                }
                break;
            case Const.ITEM_STATE.RECOVER:
                {
                }
                break;
            default:
                break;
        }
    }
    /**状态 */
    public get state(): Const.ITEM_STATE { return this._state; }
    public set state(state: Const.ITEM_STATE) {
        if (this._state == state) return;
        this._state = state;

        switch (state) {
            case Const.ITEM_STATE.READY:
                {
                    this._rig.setType(OIMO.RigidBodyType.KINEMATIC);
                }
                break;
            case Const.ITEM_STATE.SHOOT:
                {
                    this._rig.setType(OIMO.RigidBodyType.DYNAMIC);
                    let shape = this._rig.getShapeList();
                    shape.setFriction(0);
                    let vs = GameConst.data.shootVelocity;
                    this._linearVelocity.init(vs[0], vs[1], vs[2]);
                    this._rig.setLinearVelocity(this._linearVelocity);
                    this._angularVelocity.init(0, 0, 0);
                    this._rig.setAngularVelocity(this._angularVelocity);

                    this._tailingId = ComTools.playEffect(Const.EFFECT_ID.ID_9, null, null, null, this.rootObj);
                }
                break;
            case Const.ITEM_STATE.JUMP:
                {
                    this._rig.setType(OIMO.RigidBodyType.DYNAMIC);
                    let xs = GameConst.data.jumpSpeedX;
                    let ys = GameConst.data.jumpSpeedY;
                    let zs = GameConst.data.jumpSpeedZ;
                    let xv = MathEx.Random(xs[0], xs[1]);
                    let yv = MathEx.Random(ys[0], ys[1]);
                    let zv = MathEx.Random(zs[0], zs[1]);

                    this._linearVelocity.init(xv, yv, zv);
                    this._rig.setLinearVelocity(this._linearVelocity);
                }
                break;
            case Const.ITEM_STATE.FLY:
                {
                    this._rig.setType(OIMO.RigidBodyType.KINEMATIC);
                }
                break;
            case Const.ITEM_STATE.PLAYING:
                {
                    this.collisionMask = LayerDefine.Dice | LayerDefine.Plane | LayerDefine.Wall | LayerDefine.Bomb | LayerDefine.End;
                    this._rig.setType(OIMO.RigidBodyType.DYNAMIC);
                    let shape = this._rig.getShapeList();
                    shape.setFriction(GameConst.data.friction);

                    if (this._tailingId) {
                        EffectManager.instance.StopEffectByIndex(this._tailingId);
                        this._tailingId = 0;
                    }
                }
                break;
            case Const.ITEM_STATE.PAUSE:
                {
                    this._rig.getLinearVelocityTo(this._linearVelocity);
                    this._rig.getAngularVelocityTo(this._angularVelocity);
                }
                break;
            case Const.ITEM_STATE.RESUME:
                {
                    this._rig.setLinearVelocity(this._linearVelocity);
                    this._rig.setAngularVelocity(this._angularVelocity);
                    this._state = Const.ITEM_STATE.PLAYING;
                }
                break;
            case Const.ITEM_STATE.RECOVER:
                {
                }
                break;
            default:
                break;
        }
    }
    clearItem(isSave: boolean = true): void {

        this.collisionMask = LayerDefine.Dice | LayerDefine.Plane | LayerDefine.Wall | LayerDefine.Bomb;
        if (this._tailingId) {
            EffectManager.instance.StopEffectByIndex(this._tailingId);
            this._tailingId = 0;
        }
        this.stopTween();
        this.rootObj?.removeSelf();
        OIMOPhysicManager.instance.world.removeRigidBody(this._rig);
        Laya.Pool.recover(`dice_${this.ref.id}`, this);
        if (isSave) {
            // console.error("删除保存");
            LevelSaveData.instance.saveLevelData();
        }
        if(this._cacheEffectId){
            EffectManager.instance.StopEffectByIndex(this._cacheEffectId); 
        }
    }
}